  .copyright {
    padding-top: 30px;
  }
}

.footer-wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  background: #fff;
  display: none;

  @media (max-width: 450px) {
    display: block;
  }

  .copyright {
    padding: 15px;
    a {
      padding-left: 5px;
      color: #787878;
      font-weight: 500;
      transition: all 0.3s ease;
      text-decoration: none;
      &:hover {
        color: #000;
        letter-spacing: 2px;
      }
    }
  }
}

@media (max-width: 576px) {
  .tokyo_tm_contact {
    padding-bottom: 100px;
  }
  .tokyo_tm_news {
    padding-bottom: 60px;
  }
  .tokyo_tm_portfolio {
    padding-bottom: 70px;
  }
  .tokyo_tm_about {
    padding-bottom: 95px;
  }
  .tokyo_tm_home {
    padding-bottom: 90px;
    min-height: auto;
  }
}

.popup-content {
  width: auto;
  padding: 0;
  border: none;
  background: #000;
}
.modal {
  width: 900px;
  height: 600px;
  iframe,
  .frame {
    width: 100%;
    height: 100%;
  }
  .close_modal {
    position: absolute;
    cursor: pointer;
    right: -12px;
    top: -25px;
    background: #fff;
    border: none;
    outline: none;
    padding: 5px 10px;
    font-size: 20px;
    border-radius: 50%;
  }
}
